<template>
  <h3>Планирование</h3>
  <hr>
  <div v-if="categoriesAvailableForLimitation.length === 0" class="row">
    <div class="col">
      <p>У Вас нет ни одной расходной категории.</p>
      <p>Добавьте хотя бы одну расходную
        <router-link to="/categories">категорию</router-link>
      </p>
    </div>
  </div>
  <div v-else class="row">
    <h5>
      Запланировано: {{ currencyFilter(limitTotal) }}. Израсходовано: {{ currencyFilter(spendTotal) }}.
      <small
        v-if="overspendTotal > 0"
        class="red-text">
        Перерасход: {{ currencyFilter(overspendTotal) }}
      </small>
    </h5>
  </div>


  <section v-if="limitCategories.length > 0">
    <div class="row valign-wrapper">
      <div class="col s12 m4">
        <button
          :disabled="categoriesAvailableForLimitation.length === 0"
          class="btn modal-trigger"
          data-target="modalSetLimit">
          <i class="material-icons right">calculate</i>
          Установить лимит
        </button>
      </div>
      <div class="col s12 m2 offset-m3">
        <YearSelectComponent/>
      </div>
      <div class="col s12 m3">
        <MonthSelectComponent :show-all="true"/>
      </div>
    </div>
    <div class="row scrollable">
      <div v-for="cat in limitCategories" :key="cat.id" class="row">
        <div v-if="!!cat && (cat.limit > 0 || cat.records.length > 0 || cat.spend > 0)">
          <p>
            <strong>{{ cat.name }}</strong>
            {{ currencyFilter(cat['spend']) }} из {{ currencyFilter(cat['limit']) }}
            <small v-if="cat.overspend > 0" class="red-text">Перерасход на {{ currencyFilter(cat.overspend) }}</small>
          </p>
          <div class="progress">
            <div
              :class="[cat.progressColor]"
              :style="{ width: cat.progressPercent + '%' }"
              class="determinate"></div>
          </div>
          <table class="highlight centered responsive-table">
            <thead>
            <tr>
              <th>Дата</th>
              <th>Счёт</th>
              <th>Сумма</th>
              <th class="fixed-width">Комментарий</th>
              <th class="fixed-width">Действия</th>
            </tr>
            </thead>

            <tbody>
            <tr v-for="rec in cat.records" :key="rec.id">
              <td>{{ dateFilter(rec['date']) }}</td>
              <td>{{ rec['account'] }}</td>
              <td>{{ currencyFilter(rec['amount']) }}</td>
              <td>{{ rec['description'] }}</td>
              <td>
                <div class="row mb-0 flex-center">
                  <div class="col">
                    <button
                      :id="rec.id"
                      class="green btn"
                      @click="setRegular">
                      <i class="material-icons">done_all</i>
                    </button>
                  </div>
                  <div class="col">
                    <button
                      :id="rec.id"
                      :data-target="rec.type === 'transfer' ? 'modalEditTransfer' : 'modalEditTransaction'"
                      class="yellow btn modal-trigger black-text"
                      @click="setTransactionId">
                      <i class="material-icons">edit</i>
                    </button>
                  </div>
                  <div class="col">
                    <button
                      :id="rec.id"
                      class="red btn modal-trigger"
                      data-target="modalDeleteTransaction"
                      @click="setTransactionId">
                      <i class="material-icons">delete</i>
                    </button>
                  </div>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>

</template>

<script lang="ts" setup>
import MonthSelectComponent from '@/components/MonthSelectComponent.vue';
import YearSelectComponent from '@/components/YearSelectComponent.vue';
import { storeToRefs } from 'pinia';
import { useCategoriesStore } from '@/stores/categories';
import { useTransactionsStore } from '@/stores/transactions';
import { usePlanningStore } from '@/stores/limits';
import { useFiltersStore } from '@/stores/filters';
import { computed, ComputedRef } from 'vue';

const { categoriesAvailableForLimitation } = storeToRefs(useCategoriesStore());
const {
  selectedTransactionId,
  outcomeTransactions,
  transactionsMonthFilter
} = storeToRefs(useTransactionsStore());
const { changeTransactionToRegular } = useTransactionsStore();
const { limits } = storeToRefs(usePlanningStore());
const { currencyFilter, dateFilter } = useFiltersStore();

const limitCategories = computed(() => {
  return categoriesAvailableForLimitation.value.map((cat) => {
    const records = outcomeTransactions.value
      .filter((tr) => tr.categoryId === cat.id)
      .filter((tr) => tr.isForecast) || [];

    const spend: number = outcomeTransactions.value
      .filter((tr) => tr.categoryId === cat.id)
      .filter((tr) => !tr.isForecast).reduce((total, record): number => {
        // noinspection JSUnusedAssignment
        return Number((total += +record.amount).toFixed(2));
      }, 0);

    const limitInfo = limits.value.find((lim) => lim.categoryId === cat.id);

    let limit: number;
    if (!!limitInfo) {
      limit = limitInfo.limit;
    } else {
      limit = 0;
    }

    const overspend: number = (spend - limit) > 0 ? Number((spend - limit).toFixed(2)) : 0;

    const percent: number = limit > 0 ? (100 * spend) / limit : limit;
    const progressPercent: number = percent > 100 ? 100 : percent;

    const progressColor: string = percent < 80 ? 'green' : percent < 100 ? 'yellow' : 'red';

    return {
      ...cat,
      records,
      spend,
      limit,
      overspend,
      progressPercent,
      progressColor
    };
  }).sort((a, b) => {
    if (a.name > b.name) {
      return 1;
    } else if (a.name < b.name) {
      return -1;
    } else {
      return 0;
    }
  });
});

const limitTotal: ComputedRef<number> = computed((): number => {
  if (transactionsMonthFilter.value === 12) {
    let limit = limitCategories.value.reduce((total, cat) => {
      return Number((total + cat.limit).toFixed(2));
    }, 0);
    limit = limit * transactionsMonthFilter.value;
    return limit;
  } else {
    return limitCategories.value.reduce((total, cat) => {
      return Number((total + cat.limit).toFixed(2));
    }, 0);
  }
});

const spendTotal: ComputedRef<number> = computed((): number => {
  return limitCategories.value.reduce((total, cat) => {
    return Number((total + cat.spend).toFixed(2));
  }, 0);
});

const overspendTotal: ComputedRef<number> = computed((): number => {
  return limitCategories.value.reduce((total, cat) => {
    return Number((total + cat.overspend).toFixed(2));
  }, 0);
});

const setTransactionId = (event: any) => {
  let el: HTMLButtonElement;
  if (event.target.tagName === 'BUTTON') {
    el = event.target;
  } else {
    el = event.target.parentElement;
  }
  selectedTransactionId.value = el.id;
};

let counter: number = 0;

const setRegular = async (event: any) => {
  const ev = event;
  try {
    counter++;
    setTransactionId(ev);
    if (!selectedTransactionId.value) return;
    await changeTransactionToRegular();
  } catch (e) {
    console.log(e);
    if (counter < 5) {
      setTimeout(() => setRegular(ev), 100);
    }
  }
};
</script>

<style lang="scss" scoped>
.row.mb-0 {
  margin-bottom: 0;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;

  & .ml-0 {
    margin-left: unset;
  }
}

th.fixed-width {
  width: 250px;

  @media (max-width: 992px) {
    width: auto;
  }
}

.scrollable {
  height: 65vh;
  overflow-y: scroll;

  @media (max-width: 1366px) {
    height: 55vh;
  }

  &::-webkit-scrollbar {
    width: 12px;
  }

  &::-webkit-scrollbar-track {
    background-color: #c8e6c9;
    border-radius: 100px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #388e3c;
    border-radius: 100px;
  }
}
</style>