<template>
  <div class="input-field">
    <select ref="yearSelect" v-model.number="transactionsYearFilter">
      <option v-for="item in yearsSelectArray" :key="item.id" :value="item.year">{{ item.year }}</option>
    </select>
    <label>Фильтрация по годам</label>
  </div>
</template>

<script lang="ts" setup>
import { computed, ComputedRef, onMounted, onUpdated, ref } from 'vue';
import { Transaction, useTransactionsStore } from '@/stores/transactions';
import { storeToRefs } from 'pinia';
import { useStatusStore } from '@/stores/status';

const { transactions, transactionsYearFilter } = storeToRefs(useTransactionsStore());
const { isGlobalLoading } = storeToRefs(useStatusStore());
isGlobalLoading.value = true;

const yearSelect = ref();
const yearSelectInstance = ref();

interface Year {
  year: number,
  id: number
}

const yearsSelectArray: ComputedRef<Year[]> = computed(() => {
  const years: Year[] = [];
  const yearsSet: Set<number> = new Set();
  yearsSet.add(new Date().getFullYear());
  transactions.value.forEach((tr: Transaction) => {
    const trYear: number = new Date(tr.date).getFullYear();
    yearsSet.add(trYear);
  });
  const maxYear: number = Math.max.apply(null, Array.from(yearsSet)) + 1;
  yearsSet.add(maxYear);
  const yearsArray = Array.from(yearsSet).sort((a, b) => a - b);
  yearsArray.forEach((year) => {
    const newYear: Year = { year: year, id: Math.floor((Math.random() * 1000000)) };
    years.push(newYear);
  });
  isGlobalLoading.value = false;
  return years;
});

onMounted(() => {
  yearSelectInstance.value = M.FormSelect.init(yearSelect.value);
});

onUpdated(() => {
  yearSelectInstance.value = M.FormSelect.init(yearSelect.value);
});
</script>